import loading from '@/common/mixin/loading.js';
import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import uniNumberBox from '@/components/uni-ui/uni-number-box/uni-number-box.vue';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import commonList from '@/components/common/common-list.vue';

export default {
	mixins: [loading],
	components: {
		uniNavBar,
		uniNumberBox,
		commonList
	},
	data() {
		return {
			min:1,
			max:100,
			isedit: false,
			
		}
	},
	computed: {
		...mapState({
			userInfo:state=>state.user.userInfo,
			list: state => state.cart.list,
			selectedList: state => state.cart.selectedList
		}),
		...mapGetters(['checkedAll', 'totalPrice', 'disableSelectAll'])
	},
	onLoad() {
		this.getData();
		
		uni.$on('updateCart',()=>{
			this.getData()
		})
		
	},
	beforeDestroy() {
		uni.$off('updateCart');
	},
	onPullDownRefresh() {
		this.getData()
	},
	onNavigationBarButtonTap(e) {
		//this.isedit = this.isedit
		this.isedit = !this.isedit
		console.log(this.isedit)
		let pages = getCurrentPages()
		let page = pages[pages.length - 1];
		//console.log(page)











		
	
	},
	methods: {
		...mapActions(['doSelectAll', 'doDelGoods','updateCartList']),
		...mapMutations(['selectItem', 'initCartList', 'unSelectAll']),
		changeNum(e, item, index) {
			if (item.num === e) return;
			uni.showLoading({
				title: '加载中...'
			})
			this.$H.post('/product/cart/update/number/' + item.id,{
				num:e
				},{
					token: true
				}
				).then(res => {
					item.num = e;
					uni.hideLoading();
				})
		},
		// 订单结算
		orderConfirm() {
			if (this.selectedList.length === 0) {
				return uni.showToast({
					title: '请选择要结算的商品',
					icon: 'none'
				});
			}
			uni.navigateTo({
				url: '../order-confirm/order-confirm?detail=' + JSON.stringify(this.selectedList)
			});
		},
		// 获取数据
		async getData() {
			// 获取购物车数据
			this.updateCartList().then(res=>{
				uni.stopPullDownRefresh()
			}).catch(err=>{
				uni.stopPullDownRefresh()
			})
			/*
			if(this.userInfo.id  <= 0) return false
			
			await this.$H.get('/product/cart/'+this.userInfo.id,{},{
				token:true,
				toast:false
			}).then(res=>{
				console.log(JSON.stringify(res))
				this.unSelectAll()
				this.initCartList(res)
				uni.stopPullDownRefresh()
			}).catch(err=>{
				uni.showToast({
					title:err,
					icon:'none'
				})
				uni.stopPullDownRefresh()
			})
			*/
		}
	}
};
